@import "_flexboxgrid.scss";

.bio {
    @extend .col-lg-9;
    @extend .col-sm-12;
    @extend .col-md-9;

    -webkit-box-flex: 8;
    -webkit-flex-grow: 8;
    -moz-box-flex: 8;
    -ms-flex-positive: 8;
    flex-grow: 8;
    padding-right: 15px;


    &__content,
    &__title,
    &__subheader {
        text-align: right;

        @media only screen and (max-width: 62em) {
            text-align: left;
        }

    }

    &__title,
    &__subheader {
        font-family: "Lato", sans-serif;
        margin-top: 25px;
        margin-bottom: 36px;
    }
}

.pdf {
    @extend .bio;
}

.schedule {
    @extend .bio;
}

.cv {
    @extend .bio;
}

.faq {
    @extend .bio;
}