@import "_flexboxgrid.scss";

.header {
  background-color: #fff;
  min-height: 100px;
  width: 100% !important;

  @media screen and (max-width: 44em) {
    //width: 100% ;
    margin-bottom: 40px !important;



  }

  &__mobile {
    display: none !important;

    color: #280680;

    @media screen and (max-width: 44em) {
      display: inline !important;
    }
  }

  &__container {
    @extend .col-lg-9;
    @extend .col-md-9;
    @extend .col-sm-12;
    @extend .row;
    @extend .reverse;

    width: 100% !important;
    border-bottom: 0.5px solid #280680 !important;
    margin: 0 !important;

    @media screen and (max-width: 48em) {
      width: 100%;
      height: 100px !important;
    }

    &--main {
      @extend .col-lg-12;
      @extend .col-md-12;
      @extend .col-sm-12;
    }
  }
}

.nav {
  @extend .col-lg-9;
  @extend .col-md-9;
  @extend .col-sm-12;
  @extend .col-xs-12;

  @extend .row;
  @extend .reverse;

  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
  -webkit-flex-direction: row-reverse;
  -moz-box-orient: horizontal;
  -moz-box-direction: reverse;
  -ms-flex-direction: row-reverse;
  flex-direction: row-reverse;
  height: 100px;
  max-width: 55%;
  flex-wrap: nowrap !important;

  @media screen and (max-width: 44em) {
    display: none !important;
  }
}

.nav__link {
  @extend .col-xs;
  @extend .col-sm;
  @extend .col-md;
  @extend .col-lg;

  font-family: Lato, sans-serif;
  font-size: 26px;
  color: lighten(#280680, 45%);
  text-decoration: none;
  margin-left: 20px;
  position: relative;
  top: 36px;
  height: 73px;

  @media screen and (max-width: 48em) {
    margin: 5px !important;
  }

  &:hover {
    color: #280680 !important;
    text-decoration: underline;
  }

  &:visited {
    color: lighten(#280680, 45%);
  }
}

.nav__link--active {
  color: #280680 !important;
  font-weight: 700;
  text-decoration: underline;
}