html,
body {
  height: 100% !important;
  overflow-x: hidden;
}

body {
  min-height: 100% !important;
  display: flex;
  flex-direction: column;
}

.my__container {
  padding: 0 5rem;

  @media screen and (max-width: 44em) {
    padding: 0 20px;
  }
}

.flexContainer {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
  display: flex;
  flex: 1 0 auto;
}

.main,
.pdf {
  height: 100%;
  width: 100%;

}

