@import "_flexboxgrid.scss";

.imageCard {
  @extend .col-lg-3;
  @extend .col-sm-12;
  @extend .col-md-3;
  @extend .center-sm;

  -webkit-box-flex: 2;
  -webkit-flex-grow: 2;
  -moz-box-flex: 2;
  -ms-flex-positive: 2;
  flex-grow: 2;
  padding-left: 15px !important;

  @media only screen and (max-width: 62em) {
    order: -1;
    padding-top: 20px;
  }

  @media only screen and (max-width: 62em) {
    text-align: center;
  }

  &__image {
    border-radius: 100%;
    position: relative;
    top: -50px;
    width: 250px;
    height: 250px;
    border-radius: 50%;
    padding: 3px;
    border: 3px solid purple;

    @media only screen and (max-width: 62em) {
      top: 20px !important;
    }

    @media only screen and (max-width: 48em) {
      top: 20px !important;
      text-align: center !important;
    }
  }
}

.cardText {
  text-align: left;
  padding-left: 35px;

  @media only screen and (max-width: 62em) {
    text-align: center !important;

  }

  @media only screen and (max-width: 48em) {
    text-align: center !important;
  }
}